import React from 'react';
import './page.css';

import Titlebar from '../components/Titlebar/Titlebar';
var Preload = require('react-preload').Preload;

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pageNumber: 0,
      urls: this.props.urls,
      texts: this.props.texts,
    }
  }

  pageHandler = () => {
    let newPage;
    if (this.state.pageNumber === this.state.urls.length - 1) newPage = 0
    else newPage = this.state.pageNumber + 1
    this.setState({
      pageNumber: newPage,
    })
  }

  dotHandler = (idx) => {
    this.setState({
      pageNumber: idx,
    })
  }

  render() {
    return (
      <div>
      	<div className="titlebar-container"><Titlebar /></div>
      	<div className='portfolio-container'>
  	    	<div className='image-container'>
            <Preload
                images={this.state.sudokus}
                autoResolveDelay={3000}
                onError={this._handleImageLoadError}
                onSuccess={this._handleImageLoadSuccess}
                resolveOnError={true}
                mountChildren={true}
            >
                {<img src={this.state.urls[this.state.pageNumber]} className="portfolio-img" onClick={this.pageHandler} />}
            </Preload>
  	    	</div>
  	    	<div className='text-container'>
            {this.state.texts[this.state.pageNumber]}
  	    	</div>
  	    </div>
        <div className="dot-container">
          {this.state.urls.map((item, index) => (
            <span className={"dot" + (this.state.pageNumber === index ? ' selected' : '')} onClick={() => {this.dotHandler(index);}} />))
          }
        </div>
      </div>
    );
  }
}

export default Page;
