import React, { useEffect, useState } from 'react';
import './Titlebar.css';

import hamburger from '../../images/misc/hamburger.svg'
import Dropdown from 'react-bootstrap/Dropdown'

class Titlebar extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			showMenu: false,
		}
	}

	showMenu = () => {
		this.setState({
			showMenu: !this.state.showMenu
		})
	}

	handleResize = () => {
		if (window.innerWidth > 700) {
			this.setState({ showMenu: false })
		}
	}


	render() {
	  window.addEventListener('resize', this.handleResize)
	  return (
	  	<div className="titlebar-container">
	  	<div style={this.state.showMenu ? {display: 'block'} : {display: 'none'}} className="hamburger-menu" >
	  		<div className="hamburger-menu__text"><a href='/' className='link'>Work</a></div>
	  		<div className="hamburger-menu__text"><a href='/about' className='link'>About</a></div>
	  		<div className="hamburger-menu__text"><a href='/contact' className='link'>Contact</a></div>
	  	</div>
	  	<div className="titlebar" style={this.state.showMenu ? {top: '105px'} : {top: '0px'}}>
	  		<div className="titlebar-left">
	  			<div className="nav-item" style={{ marginLeft: '5vw' }}><a href='/code' className='link'>Code</a></div>
	  			<div className="nav-item"><a href='/design' className='link'>Design</a></div>
	  			<div className="nav-item"><a href='/' className='link'>All</a></div>
	  		</div>
		  	<div className="titlebar-center">
			    <div className="title">
			    	URVASHI UBEROY
			    </div>
			    <div className="byline">
			    	Coder | Designer
			    </div>
		    </div>
		    <div className="titlebar-right">
		    	<div className="nav-item">About</div>
	  			<div className="nav-item" style={{ marginRight: '5vw' }}>Contact</div>
		    	<div className="hamburger-container">
		    		<img src={hamburger} className="hamburger" onClick={this.showMenu}/>
		    	</div>
		    </div>
	    </div>
	    </div>
	  );
	}
}

export default Titlebar;